import { Component } from '@angular/core';
import { BaseComponentWithTheme } from '../../utils';
import type { SelectedAriaAttributes, IconAriaAttribute, IconColor, IconName, IconSize, Theme } from '../types';

@Component({
  selector: 'p-icon,[p-icon]',
  template: '<ng-content />',
  inputs: ['aria', 'color', 'lazy', 'name', 'size', 'source', 'theme']
})
export class PIcon extends BaseComponentWithTheme {
  aria?: SelectedAriaAttributes<IconAriaAttribute>;
  color?: IconColor;
  /** @deprecated */
  lazy?: boolean;
  name?: IconName;
  size?: IconSize;
  source?: string;
  declare theme?: Theme;
}