import { Component, EventEmitter } from '@angular/core';
import { BaseComponentWithTheme } from '../../utils';
import type { SelectedAriaAttributes, FlyoutNavigationAriaAttribute, FlyoutNavigationUpdateEventDetail, Theme } from '../types';

@Component({
  selector: 'p-flyout-navigation,[p-flyout-navigation]',
  template: '<ng-content />',
  inputs: ['activeIdentifier', 'aria', 'open', 'theme'],
  outputs: ['dismiss', 'update']
})
export class PFlyoutNavigation extends BaseComponentWithTheme {
  activeIdentifier?: string | undefined;
  aria?: SelectedAriaAttributes<FlyoutNavigationAriaAttribute>;
  open?: boolean;
  declare theme?: Theme;
  dismiss = new EventEmitter<CustomEvent<void>>();
  update = new EventEmitter<CustomEvent<FlyoutNavigationUpdateEventDetail>>();
}