import { Component, EventEmitter } from '@angular/core';
import { BaseComponentWithTheme } from '../../utils';
import type { TabsGradientColor, TabsGradientColorScheme, TabsUpdateEventDetail, BreakpointCustomizable, TabsSize, Theme, TabsWeight } from '../types';

@Component({
  selector: 'p-tabs,[p-tabs]',
  template: '<ng-content />',
  inputs: ['activeTabIndex', 'gradientColor', 'gradientColorScheme', 'size', 'theme', 'weight'],
  outputs: ['tabChange', 'update']
})
export class PTabs extends BaseComponentWithTheme {
  activeTabIndex?: number;
  gradientColor?: TabsGradientColor;
  /** @deprecated */
  gradientColorScheme?: TabsGradientColorScheme;
  size?: BreakpointCustomizable<TabsSize>;
  declare theme?: Theme;
  weight?: TabsWeight;
  /** @deprecated */
  tabChange = new EventEmitter<CustomEvent<TabsUpdateEventDetail>>();
  update = new EventEmitter<CustomEvent<TabsUpdateEventDetail>>();
}