import { Component, EventEmitter } from '@angular/core';
import { BaseComponentWithTheme } from '../../utils';
import type { SelectDropdownDirection, BreakpointCustomizable, SelectUpdateEventDetail, SelectState, Theme } from '../types';

@Component({
  selector: 'p-select,[p-select]',
  template: '<ng-content />',
  inputs: ['description', 'disabled', 'dropdownDirection', 'hideLabel', 'label', 'message', 'name', 'required', 'state', 'theme', 'value'],
  outputs: ['update']
})
export class PSelect extends BaseComponentWithTheme {
  description?: string;
  disabled?: boolean;
  dropdownDirection?: SelectDropdownDirection;
  hideLabel?: BreakpointCustomizable<boolean>;
  label?: string;
  message?: string;
  name: string;
  required?: boolean;
  state?: SelectState;
  declare theme?: Theme;
  value?: string;
  update = new EventEmitter<CustomEvent<SelectUpdateEventDetail>>();
}