import { Component, EventEmitter } from '@angular/core';
import { BaseComponentWithTheme } from '../../utils';
import type { AccordionUpdateEventDetail, BreakpointCustomizable, AccordionSize, AccordionTag, Theme } from '../types';

@Component({
  selector: 'p-accordion,[p-accordion]',
  template: '<ng-content />',
  inputs: ['compact', 'heading', 'open', 'size', 'tag', 'theme'],
  outputs: ['accordionChange', 'update']
})
export class PAccordion extends BaseComponentWithTheme {
  compact?: boolean;
  heading?: string;
  open?: boolean;
  size?: BreakpointCustomizable<AccordionSize>;
  tag?: AccordionTag;
  declare theme?: Theme;
  /** @deprecated */
  accordionChange = new EventEmitter<CustomEvent<AccordionUpdateEventDetail>>();
  update = new EventEmitter<CustomEvent<AccordionUpdateEventDetail>>();
}