import { Component, EventEmitter } from '@angular/core';
import { BaseComponentWithTheme } from '../../utils';
import type { MultiSelectDropdownDirection, BreakpointCustomizable, MultiSelectUpdateEventDetail, MultiSelectState, Theme } from '../types';

@Component({
  selector: 'p-multi-select,[p-multi-select]',
  template: '<ng-content />',
  inputs: ['description', 'disabled', 'dropdownDirection', 'hideLabel', 'label', 'message', 'name', 'required', 'state', 'theme', 'value'],
  outputs: ['update']
})
export class PMultiSelect extends BaseComponentWithTheme {
  description?: string;
  disabled?: boolean;
  dropdownDirection?: MultiSelectDropdownDirection;
  hideLabel?: BreakpointCustomizable<boolean>;
  label?: string;
  message?: string;
  name: string;
  required?: boolean;
  state?: MultiSelectState;
  declare theme?: Theme;
  value?: string[];
  update = new EventEmitter<CustomEvent<MultiSelectUpdateEventDetail>>();
}