import { Component, EventEmitter } from '@angular/core';
import { BaseComponentWithTheme } from '../../utils';
import type { TabsBarGradientColor, TabsBarGradientColorScheme, TabsBarUpdateEventDetail, BreakpointCustomizable, TabsBarSize, Theme, TabsBarWeight } from '../types';

@Component({
  selector: 'p-tabs-bar,[p-tabs-bar]',
  template: '<ng-content />',
  inputs: ['activeTabIndex', 'gradientColor', 'gradientColorScheme', 'size', 'theme', 'weight'],
  outputs: ['tabChange', 'update']
})
export class PTabsBar extends BaseComponentWithTheme {
  activeTabIndex?: number | undefined;
  gradientColor?: TabsBarGradientColor;
  /** @deprecated */
  gradientColorScheme?: TabsBarGradientColorScheme;
  size?: BreakpointCustomizable<TabsBarSize>;
  declare theme?: Theme;
  weight?: TabsBarWeight;
  /** @deprecated */
  tabChange = new EventEmitter<CustomEvent<TabsBarUpdateEventDetail>>();
  update = new EventEmitter<CustomEvent<TabsBarUpdateEventDetail>>();
}