import { Component, EventEmitter } from '@angular/core';
import { BaseComponentWithTheme } from '../../utils';
import type { InlineNotificationActionIcon, InlineNotificationState, Theme } from '../types';

@Component({
  selector: 'p-inline-notification,[p-inline-notification]',
  template: '<ng-content />',
  inputs: ['actionIcon', 'actionLabel', 'actionLoading', 'description', 'dismissButton', 'heading', 'persistent', 'state', 'theme'],
  outputs: ['action', 'dismiss']
})
export class PInlineNotification extends BaseComponentWithTheme {
  actionIcon?: InlineNotificationActionIcon;
  actionLabel?: string;
  actionLoading?: boolean;
  description?: string;
  dismissButton?: boolean;
  heading?: string;
  /** @deprecated */
  persistent?: boolean;
  state?: InlineNotificationState;
  declare theme?: Theme;
  action = new EventEmitter<CustomEvent<void>>();
  dismiss = new EventEmitter<CustomEvent<void>>();
}