import { Component } from '@angular/core';
import { BaseComponentWithTheme } from '../../utils';
import type { FieldsetLabelSize, FieldsetState, Theme } from '../types';

@Component({
  selector: 'p-fieldset,[p-fieldset]',
  template: '<ng-content />',
  inputs: ['label', 'labelSize', 'message', 'required', 'state', 'theme']
})
export class PFieldset extends BaseComponentWithTheme {
  label?: string;
  labelSize?: FieldsetLabelSize;
  message?: string;
  required?: boolean;
  state?: FieldsetState;
  declare theme?: Theme;
}