import { Component, EventEmitter } from '@angular/core';
import { BaseComponentWithTheme } from '../../utils';
import type { BreakpointCustomizable, PinCodeLength, PinCodeUpdateEventDetail, PinCodeState, Theme, PinCodeType } from '../types';

@Component({
  selector: 'p-pin-code,[p-pin-code]',
  template: '<ng-content />',
  inputs: ['description', 'disabled', 'hideLabel', 'label', 'length', 'loading', 'message', 'name', 'required', 'state', 'theme', 'type', 'value'],
  outputs: ['update']
})
export class PPinCode extends BaseComponentWithTheme {
  description?: string;
  disabled?: boolean;
  hideLabel?: BreakpointCustomizable<boolean>;
  label?: string;
  length?: PinCodeLength;
  loading?: boolean;
  message?: string;
  name?: string;
  required?: boolean;
  state?: PinCodeState;
  declare theme?: Theme;
  type?: PinCodeType;
  value?: string;
  update = new EventEmitter<CustomEvent<PinCodeUpdateEventDetail>>();
}