import { Component, EventEmitter } from '@angular/core';
import { BaseComponentWithTheme } from '../../utils';
import type { SelectedAriaAttributes, FlyoutAriaAttribute, FlyoutPosition, Theme } from '../types';

@Component({
  selector: 'p-flyout,[p-flyout]',
  template: '<ng-content />',
  inputs: ['aria', 'open', 'position', 'theme'],
  outputs: ['dismiss']
})
export class PFlyout extends BaseComponentWithTheme {
  aria?: SelectedAriaAttributes<FlyoutAriaAttribute>;
  open: boolean;
  position?: FlyoutPosition;
  declare theme?: Theme;
  dismiss = new EventEmitter<CustomEvent<void>>();
}