import { Component, EventEmitter } from '@angular/core';
import { BaseComponentWithTheme } from '../../utils';
import type { SegmentedControlBackgroundColor, BreakpointCustomizable, SegmentedControlColumns, SegmentedControlUpdateEventDetail, Theme } from '../types';

@Component({
  selector: 'p-segmented-control,[p-segmented-control]',
  template: '<ng-content />',
  inputs: ['backgroundColor', 'columns', 'theme', 'value'],
  outputs: ['segmentedControlChange', 'update']
})
export class PSegmentedControl extends BaseComponentWithTheme {
  /** @deprecated */
  backgroundColor?: SegmentedControlBackgroundColor;
  columns?: BreakpointCustomizable<SegmentedControlColumns>;
  declare theme?: Theme;
  value?: string | number;
  /** @deprecated */
  segmentedControlChange = new EventEmitter<CustomEvent<SegmentedControlUpdateEventDetail>>();
  update = new EventEmitter<CustomEvent<SegmentedControlUpdateEventDetail>>();
}