import { Component, EventEmitter } from '@angular/core';
import { BaseComponentWithTheme } from '../../utils';
import type { TableUpdateEventDetail, Theme } from '../types';

@Component({
  selector: 'p-table,[p-table]',
  template: '<ng-content />',
  inputs: ['caption', 'theme'],
  outputs: ['sortingChange', 'update']
})
export class PTable extends BaseComponentWithTheme {
  caption?: string;
  declare theme?: Theme;
  /** @deprecated */
  sortingChange = new EventEmitter<CustomEvent<TableUpdateEventDetail>>();
  update = new EventEmitter<CustomEvent<TableUpdateEventDetail>>();
}