import { Component, EventEmitter } from '@angular/core';
import { BaseComponentWithTheme } from '../../utils';
import type { BreakpointCustomizable, SwitchAlignLabel, SwitchUpdateEventDetail, Theme } from '../types';

@Component({
  selector: 'p-switch,[p-switch]',
  template: '<ng-content />',
  inputs: ['alignLabel', 'checked', 'disabled', 'hideLabel', 'loading', 'stretch', 'theme'],
  outputs: ['switchChange', 'update']
})
export class PSwitch extends BaseComponentWithTheme {
  alignLabel?: BreakpointCustomizable<SwitchAlignLabel>;
  checked?: boolean;
  disabled?: boolean;
  hideLabel?: BreakpointCustomizable<boolean>;
  loading?: boolean;
  stretch?: BreakpointCustomizable<boolean>;
  declare theme?: Theme;
  /** @deprecated */
  switchChange = new EventEmitter<CustomEvent<SwitchUpdateEventDetail>>();
  update = new EventEmitter<CustomEvent<SwitchUpdateEventDetail>>();
}