import { Component, EventEmitter } from '@angular/core';
import { BaseComponentWithTheme } from '../../utils';
import type { CarouselAlignHeader, SelectedAriaAttributes, CarouselAriaAttribute, BreakpointCustomizable, CarouselHeadingSize, CarouselInternationalization, CarouselUpdateEventDetail, Theme, CarouselWidth } from '../types';

@Component({
  selector: 'p-carousel,[p-carousel]',
  template: '<ng-content />',
  inputs: ['activeSlideIndex', 'alignHeader', 'aria', 'description', 'disablePagination', 'heading', 'headingSize', 'intl', 'pagination', 'rewind', 'skipLinkTarget', 'slidesPerPage', 'theme', 'width', 'wrapContent'],
  outputs: ['carouselChange', 'update']
})
export class PCarousel extends BaseComponentWithTheme {
  activeSlideIndex?: number;
  alignHeader?: CarouselAlignHeader;
  aria?: SelectedAriaAttributes<CarouselAriaAttribute>;
  description?: string;
  /** @deprecated */
  disablePagination?: BreakpointCustomizable<boolean>;
  heading?: string;
  headingSize?: CarouselHeadingSize;
  intl?: CarouselInternationalization;
  pagination?: BreakpointCustomizable<boolean>;
  rewind?: boolean;
  skipLinkTarget?: string;
  slidesPerPage?: BreakpointCustomizable<number> | "auto";
  declare theme?: Theme;
  width?: CarouselWidth;
  /** @deprecated */
  wrapContent?: boolean;
  /** @deprecated */
  carouselChange = new EventEmitter<CustomEvent<CarouselUpdateEventDetail>>();
  update = new EventEmitter<CustomEvent<CarouselUpdateEventDetail>>();
}