import { Component, EventEmitter } from '@angular/core';
import { BaseComponentWithTheme } from '../../utils';
import type { TextFieldWrapperActionIcon, BreakpointCustomizable, TextFieldWrapperState, Theme, TextFieldWrapperUnitPosition } from '../types';

@Component({
  selector: 'p-text-field-wrapper,[p-text-field-wrapper]',
  template: '<ng-content />',
  inputs: ['actionIcon', 'actionLoading', 'description', 'hideLabel', 'label', 'message', 'showCharacterCount', 'showCounter', 'showPasswordToggle', 'state', 'submitButton', 'theme', 'unit', 'unitPosition'],
  outputs: ['action']
})
export class PTextFieldWrapper extends BaseComponentWithTheme {
  actionIcon?: TextFieldWrapperActionIcon;
  actionLoading?: boolean;
  description?: string;
  hideLabel?: BreakpointCustomizable<boolean>;
  label?: string;
  message?: string;
  /** @deprecated */
  showCharacterCount?: boolean;
  showCounter?: boolean;
  showPasswordToggle?: boolean;
  state?: TextFieldWrapperState;
  submitButton?: boolean;
  declare theme?: Theme;
  unit?: string;
  unitPosition?: TextFieldWrapperUnitPosition;
  action = new EventEmitter<CustomEvent<void>>();
}