import { Component, EventEmitter } from '@angular/core';
import { BaseComponentWithTheme } from '../../utils';
import type { SelectedAriaAttributes, ModalAriaAttribute, ModalBackdrop, BreakpointCustomizable, Theme } from '../types';

@Component({
  selector: 'p-modal,[p-modal]',
  template: '<ng-content />',
  inputs: ['aria', 'backdrop', 'disableBackdropClick', 'disableCloseButton', 'dismissButton', 'fullscreen', 'heading', 'open', 'theme'],
  outputs: ['close', 'dismiss']
})
export class PModal extends BaseComponentWithTheme {
  aria?: SelectedAriaAttributes<ModalAriaAttribute>;
  backdrop?: ModalBackdrop;
  disableBackdropClick?: boolean;
  /** @deprecated */
  disableCloseButton?: boolean;
  dismissButton?: boolean;
  fullscreen?: BreakpointCustomizable<boolean>;
  heading?: string;
  open: boolean;
  declare theme?: Theme;
  /** @deprecated */
  close = new EventEmitter<CustomEvent<void>>();
  dismiss = new EventEmitter<CustomEvent<void>>();
}