import { Component } from '@angular/core';
import { BaseComponentWithTheme } from '../../utils';
import type { DisplayAlign, DisplayColor, BreakpointCustomizable, DisplaySize, DisplayTag, Theme } from '../types';

@Component({
  selector: 'p-display,[p-display]',
  template: '<ng-content />',
  inputs: ['align', 'color', 'ellipsis', 'size', 'tag', 'theme']
})
export class PDisplay extends BaseComponentWithTheme {
  align?: DisplayAlign;
  color?: DisplayColor;
  ellipsis?: boolean;
  size?: BreakpointCustomizable<DisplaySize>;
  tag?: DisplayTag;
  declare theme?: Theme;
}