import { Component } from '@angular/core';
import { BaseComponentWithTheme } from '../../utils';
import type { TextAlign, TextColor, BreakpointCustomizable, TextSize, TextTag, Theme, TextWeight } from '../types';

@Component({
  selector: 'p-text,[p-text]',
  template: '<ng-content />',
  inputs: ['align', 'color', 'ellipsis', 'size', 'tag', 'theme', 'weight']
})
export class PText extends BaseComponentWithTheme {
  align?: TextAlign;
  color?: TextColor;
  ellipsis?: boolean;
  size?: BreakpointCustomizable<TextSize>;
  tag?: TextTag;
  declare theme?: Theme;
  weight?: TextWeight;
}