import { Component, EventEmitter } from '@angular/core';
import { BaseComponentWithTheme } from '../../utils';
import type { BannerState, Theme, BannerWidth } from '../types';

@Component({
  selector: 'p-banner,[p-banner]',
  template: '<ng-content />',
  inputs: ['description', 'dismissButton', 'heading', 'open', 'persistent', 'state', 'theme', 'width'],
  outputs: ['dismiss']
})
export class PBanner extends BaseComponentWithTheme {
  description?: string;
  dismissButton?: boolean;
  heading?: string;
  open: boolean;
  /** @deprecated */
  persistent?: boolean;
  state?: BannerState;
  declare theme?: Theme;
  /** @deprecated */
  width?: BannerWidth;
  dismiss = new EventEmitter<CustomEvent<void>>();
}